.sidebar {
    visibility:hidden;
    width: 300px;
    height: 100%;
    position: absolute;
    background: #FFFFFF;
    box-shadow: 0px 10px 40px rgba(29, 33, 38, 0.13), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 0px;
    left: 0px;
    top: 0px;
    display: flex;
    flex-direction: column;
    padding-top: 20px;
    z-index: 1;
}

.sidebar.open {
  visibility: visible;

}

.background {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;

    background: #000000;
    opacity: 0.3;

}

.nav {
display: flex;
    flex-direction: column;
    margin-left: 30px;

}
.NavLink {
    padding: 20px;
}

.nav a {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    text-decoration: none;
    /* identical to box height */


    color: #000000;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    text-decoration-line: underline;
    color: #0059B2;
    padding: 20px;

}
.close {
    margin-left: 80%;
    border-style: none;
    background-color: white;





}

.close img {
    width:30px;
    height: 30px;
}
